import * as yup from 'yup';
export const validateEx1 = yup.object().shape({
  sum: yup
    .number()
    .typeError('Должен быть число')
    .required('Объязательный элемент'),
  percent: yup
    .number()
    .typeError('Должен быть число')
    .required('Объязательный элемент'),
  date: yup
    .date()
    .typeError('Должен быть дата')
    .required('Объязательный элемент'),
  month: yup
    .number()
    .typeError('Должен быть число')
    .max(100, 'Максимально число 100')
    .required('Объязательный элемент'),
  period: yup
    .string()
    .typeError('Должен быть строкой')
    .required('Объязательный элемент'),
});
